@import url(https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;1,100;1,200;1,300;1,400;1,500;1,600&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Rubik:ital,wght@0,300;0,400;0,500;1,300;1,400;1,500&display=swap);
*{margin:0;padding:0;box-sizing:border-box}html{font-size:62.5%}@media(max-width: 75em){html{font-size:56.25%}}@media(max-width: 56.25em){html{font-size:50%}}@media(min-width: 112.5em){html{font-size:75%}}body{font-family:"Montserrat",sans-serif}.TodoScreen{max-width:35rem;margin:0 auto;height:auto;padding:2rem;background-color:#dd3aaa;border-radius:1rem;box-shadow:5px 5px 5px #643281}.Divider{margin-top:2rem;border-bottom:1px solid #fdfdfd;height:2px}.Test-title{color:#416d4e}.TodoForm{margin-top:3.2rem;display:flex;flex-direction:column;justify-content:flex-start;align-items:flex-start}.TodoForm span{font-size:1.6rem;font-weight:400;margin-bottom:.5rem}.TodoForm form input{border:none;padding:10px}.TodoForm form button{border:1px solid #fff;background-color:#dd3aaa;color:#fff;padding:9px;transition:all 2s ease-in-out}.TodoForm form button:hover{background-color:#510564}.TodoItem_Container{display:flex;justify-content:space-between;background-color:#eadff06e;padding:1rem;margin:1rem 0}.TodoItem_Container li{list-style:none;font-weight:500}.TodoItem_task li{cursor:pointer}.isCompleted{text-decoration:line-through;color:#416d4e}.fa-pencil-alt{margin-right:2rem;cursor:pointer}.fa-trash-alt{cursor:pointer}.TodoEdit_form{margin-top:1.6rem;display:flex}.TodoEdit_form input{width:100%;border:none;padding:10px}.TodoEdit_form button{border:1px solid #fff;background-color:#dd3aaa;color:#fff;padding:9px;transition:all 2s ease-in-out}.TodoEdit_form button:hover{background-color:#510564}.App{text-align:center;min-height:100vh;background-image:linear-gradient(to right bottom, #ed2a90, #dd3aaa, #c74bc1, #a95bd5, #8269e3, #657df1, #418ffa, #009fff, #00baff, #00d2ff, #00e7fc, #5ffbf1);padding:1rem;font-family:"Rubik",sans-serif;color:#f0efef;font-size:1.6rem;font-weight:300}
